import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { GridComponent } from 'src/app/shared/component/grid/grid.component';
import { DocumentType } from 'src/app/shared/enum/document-type.enum';
import icClose from '@iconify/icons-ic/twotone-close';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material';
import { GridLichSuKCB } from '../../tiep-nhan-benh-nhan.model';

@Component({
  selector: 'app-popup-lich-su-kham-chua-benh',
  templateUrl: './popup-lich-su-kham-chua-benh.component.html',
  styleUrls: ['./popup-lich-su-kham-chua-benh.component.scss']
})
export class PopupLichSuKhamChuaBenhComponent implements OnInit {

  icClose = icClose;
  gridColumns: any[] = [];
  gridData: any;
  documentType: DocumentType;
  
  @ViewChild('grid', { static: false }) grid: GridComponent;

  constructor(@Inject(MAT_DIALOG_DATA) public data: Array<GridLichSuKCB>
  , private dialog: MatDialog, public dialogRef: MatDialogRef<PopupLichSuKhamChuaBenhComponent>) { }

  ngOnInit() {
    this.documentType = DocumentType.YeuCauTiepNhan;
    this.gridColumns = [
      { Field: "STT", Title: "STT", Width: 45, Sortable: true },
      { Field: "MaTheBHYT", Title: "M th BHYT", Width: 150, Sortable: false },
      { Field: "HoVaTen", Title: "H v tn", MinWidth: 150, Sortable: false },
      { Field: "NgayVaoVien", Title: "Ngy vo vin", Width: 156, Sortable: false },
      { Field: "NgayRaVien", Title: "Ngy ra vin", Width: 156, Sortable: false },
      { Field: "CoSoKCB", Title: "C s KCB", Width: 180, Sortable: false },
      { Field: "KetQuaDieuTri", Title: "Kt qu iu tr", Width: 100, Sortable: false },
      { Field: "LyDoVaoVien", Title: "L do vo vin", Width: 100, Sortable: false },
      { Field: "TinhTrangRaVien", Title: "Tnh trng ra vin", Width: 100, Sortable: false },
    ];

    this.setDataForGrid();

  }

  setDataForGrid() {
    this.gridData = {
      data: this.data,
      total: this.data.length
    };

    if (this.grid != undefined) {
      this.grid.setDataSource();
    }

  }

  close(answer: string) {
    this.dialogRef.close(answer);
  }

}
